.header {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.header-logo {
  padding-left: 15px;
}

.header-cart-modal {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
  outline: 0;
  padding-top: 10%;
}

.modal-body-cart {
  overflow: hidden;
  height: 400px;
  overflow-y: auto;
}

.modal-item {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}

.shoes-list-item {
  width: 200px;
}

.item-image img {
  width: 100%;
}
.item-image-modal img {
  width: 100%;
  height: 100%;
}

.alert-cart {
  position: fixed;
  top: 5%;
  right: 7%;
  z-index: 1051;
}

ul {
  padding: 0;
}
